import { h } from '@stencil/core';
import {
  MtEspecialidadesEnum,
  MtProdutosEnum,
} from '../../../../global/typescript/enums';

export const markdown = (
  produto?: string,
  especialidade?: string,
  _apostila?: string,
  _parte?: string,
  _ano?: string,
) => {
  if (produto === MtProdutosEnum.MEDCURSO) {
    if (especialidade === MtEspecialidadesEnum.NEF) {
      return (
        <div class="mt-quadro-03__container">
          <h3 class="mt-quadro-03__title">
            <slot name="title"></slot>
          </h3>
          <p class="mt-quadro-03__text">
            <slot name="text"></slot>
          </p>
          <div class="mt-quadro-03__list">
            <slot name="list"></slot>
          </div>
        </div>
      );
    }
  }

  return (
    <p>
      <slot></slot>
    </p>
  );
};
